                            <div class="label" style="font-size: 18px;">推荐宣传图<span class="remark ml15">尺寸350px*450px，格式jpg 分辨率72像素/英寸，不大于3MB</span>
                            </div>
                            <el-upload
                                    action=""
                                    class="file-uploader"
                                    action="https://jsonplaceholder.typicode.com/posts/"
                                    :show-file-list="false"
                                    :on-success="handleFileSuccess"
