<script>
    import vHead from "@/components/Header.vue";

    export default {
        name: "Step2",
        components: {
            vHead
        },
        data: function () {
            return {
                form: {
                    icon: '',
                    banner_url: '',
                    recommend_url: '',
                    video_url: '',
                    crowd_funding_status: '0',
                }
            };
        },
        methods: {
            uploadIcon(req) {
                this.form.icon = {
                    path: URL.createObjectURL(req.file),
                    req: req
                };
            },
            beforeFileUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 <= 3;

                if (!isJPG) {
                    this.$message.error('上传ICON图片只能是JPG格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传ICON图片大小不能超过3MB!');
                }
                return isJPG && isLt2M;
            },

            handleFileSuccess(res, file) {
                this.iconUrl = URL.createObjectURL(file.raw);
            },
            beforeFileUpload(file) {
                const isPNG = file.type === 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isPNG) {
                    this.$message.error('上传ICON图片只能是 PNG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传ICON图片大小不能超过 2MB!');
                }
                return isPNG && isLt2M;
            },
            goBack() {
                history.back();
            },
            submitForm() {
                this.$router.push('/project/add_step3');
            },
        },
    }
</script>

